    methods: {
        submit() {
            auth
                .register(this.name, this.email, this.password)
                .then(response => {
                    this.$router.push('/')
                    this.loading = true;
                })
                .catch(err => {
                    this.emailErrors.push("Account with this email already exists.");
                    this.loading = false;
                });
        }
    }
}
</script>
